'use client';

import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Image from "next/image";

const posters = [
  {
    id: 1,
    title: "Nagano Prefecture",
    image: "/images/poster1.jpg",
  },
  {
    id: 2,
    title: "Marrakech Merzouga",
    image: "/images/poster2.jpg",
  },
  {
    id: 3,
    title: "Yosemite National Park",
    image: "/images/poster3.jpg",
  },
  {
    id: 4,
    title: "Los Lances Beach",
    image: "/images/poster5.jpg",
  },
];

export default function ExpandingPosterCarousel() {
  const [active, setActive] = useState(0);

  return (
    <div className="relative h-screen w-full overflow-hidden bg-black text-white">
      {/* Background layer */}
      <AnimatePresence mode="wait">
        <motion.div
          key={posters[active].id}
          className="absolute inset-0"
          initial={{ opacity: 0, scale: 1.05 }}
          animate={{ opacity: 1, scale: 1 }}
          exit={{ opacity: 0, scale: 0.95 }}
          transition={{ duration: 1 }}
        >
          <Image
            src={posters[active].image}
            alt={posters[active].title}
            fill
            className="object-cover"
            priority
          />
          <div className="absolute inset-0 bg-black/40" /> {/* dark overlay */}
        </motion.div>
      </AnimatePresence>

      {/* Title + text */}
      <div className="absolute top-1/4 left-10 z-10">
        <h2 className="text-5xl font-bold">{posters[active].title}</h2>
        <button className="mt-4 px-6 py-2 bg-white text-black rounded-full">
          Discover Location
        </button>
      </div>

      {/* Poster carousel */}
      <div className="absolute bottom-10 left-1/2 z-20 flex -translate-x-1/2 gap-6">
        {posters.map((poster, i) => (
          <motion.div
            key={poster.id}
            onClick={() => setActive(i)}
            className={`relative w-48 h-64 cursor-pointer overflow-hidden rounded-xl ${
              active === i ? "ring-4 ring-white" : "opacity-70"
            }`}
            whileHover={{ scale: 1.05 }}
          >
            <Image
              src={poster.image}
              alt={poster.title}
              fill
              className="object-cover"
            />
            <div className="absolute bottom-2 left-2 text-sm font-semibold drop-shadow-lg">
              {poster.title}
            </div>
          </motion.div>
        ))}
      </div>
    </div>
  );
}
